.gloableIDO {
  width: 100vw !important;
  background: no-repeat center url(../../assets/images/pg_dark.png);
  background-size: 100% 100% !important;
  color: rgb(254, 254, 254) !important;
  // filter: contrast(110%);
  background-attachment: fixed !important;
  cursor: url(../../assets/images/mouse.png), auto !important;
  
}
.containIDO {
  width: 100% !important;
  padding-top: 6.5rem !important;
  padding-bottom: 3rem !important;
  padding-left: 4rem !important;
  padding-right: 4rem !important;
}

.taglib { 
  padding-top: 2rem !important;
  padding-bottom: 1rem !important;
}


.baseFontFamily {
  font-family: "Exotc350-Bd-BT-Bold" !important;
  color: #4f13a9 !important;
  .MuiOutlinedInput-notchedOutline {
    border-color: #a659ff !important;
  }
}
.baseFontFamilyACreed {
  font-family: "Exotc350-Bd-BT-Bold" !important;
  color: #FCFCFC !important;
  .MuiOutlinedInput-notchedOutline {
    border-color: #2c2628 !important;
  }
}

.IDOBtn {
  color: rgb(96, 219, 204) !important;
  border: 3px solid rgb(38, 99, 80) !important;
  cursor: url(../../assets/images/mouseSelect.png) 14 0, pointer !important;
}
.BurnACreedBtn { 
  color: #FCFCFC !important;
  border: 3px solid #2c2628 !important;
  cursor: url(../../assets/images/mouseSelect.png) 14 0, pointer !important;
}

.MetaMaskLink { 
  cursor: url(../../assets/images/mouseSelect.png) 14 0, pointer !important;
  border: 2px solid #13a856 !important;
  border-radius: 10px !important;
  text-decoration: unset !important;
}
.MetaMaskLink:hover { 
  background-color: #1d3645 !important;
  border: 2px solid #c9529b !important;
  box-shadow: 0px 3px 16px 0px #000000 !important;
}
.MetaMaskGif { 
  width: 4rem !important;
}
.MetaMaskDesc{ 
  padding-right: 1rem !important;
  color: rgb(246, 133, 27) !important;
  font-weight: bolder !important;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif !important;
  font-size: large !important;
}


input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none !important;
}
input[type="number"] {
  -moz-appearance: textfield !important;
}

.divideR {
  height: 2px;
  background: linear-gradient(to right, #ffffff 7.5px, transparent 7.5px) !important;
  background-size: 15px 100% !important;
}

.web3modal-modal-container { 
  * {
  outline: none !important;
  -ms-overflow-style: none !important; 
  scrollbar-width: none !important; 
}

*::-webkit-scrollbar {
	display: none !important;
} 
}
.web3modal-modal-card { 
  background-color: #123b4f !important;
  .web3modal-provider-container { 
    background-color: #123b4f !important;
  }
}

@media only screen and (max-width: 800px) {
  .AcItem {
    width: 100% !important;
    max-width: none !important;
    padding: 10px !important;
  }
  .AcItem-Bottom { 
    margin-top: 2rem !important;
  }

  .gloableIDO {
    background-image: unset !important;
    background-color: #172131 !important;
  }
  .containIDO { 
    padding-bottom: 0 !important;
  }

  .web3modal-modal-container { 
    padding: 8rem 3rem !important;
    margin-top: 3rem !important;
  }
  .web3modal-modal-card { 
    background-color: #172131 !important;
    .web3modal-provider-container { 
      background-color: #172131 !important;
    }
  }

}


@media only screen and (max-width: 550px) { 

  .containIDO {
    padding-left: 1rem !important;
    padding-right: 1rem !important;
  }

}